.form {
  box-sizing: border-box;
  width: 100%;
  padding: 56px 30px 30px 0;
  background-color: $color-sapphire;
  color: $color-default-white;

  @include vp-1199 {
    place-self: start center;
    width: 100%;
    padding: 62px 45px 68px 45px;
  }

  @include vp-767 {
    padding: 64px 15px 67px 15px;
  }

  h2 {
    font-family: 'Alumni Sans';
    font-style: normal;
    font-weight: 600;
    margin: 0;
    letter-spacing: 0.03em;
    margin-bottom: 13px;
    font-weight: 800;
    font-size: 54px;
    line-height: 156%;
    color: $color-default-white;

    @include vp-1199 {
      width: 74%;
      margin: 0 auto;
      margin-bottom: 27px;
      font-weight: 700;
      font-size: 42px;
      line-height: 50px;
    }

    @include vp-767 {
      width: 100%;
      margin-bottom: 29px;
      font-weight: 600;
      font-size: 32px;
      line-height: 38px;
    }
  }

  form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 30px;
    row-gap: 28px;
    max-width: 410px;

    @include vp-1199 {
      max-width: none;
      width: 74%;
      padding-right: 30px;
      margin: 0 auto;
      column-gap: 60px;
    }

    @include vp-767 {
      grid-template-columns: 1fr;
      width: 100%;
      padding: 0;
    }
  }

  label {
    position: relative;
  }

  span {
    position: absolute;
    top: 10px;
    font-size: 14px;
    line-height: 16px;

    transition: $trans-default;
    transition-property: top;

    @include vp-1199 {
      line-height: 18px;
    }
  }

  input {
    display: block;
    box-sizing: border-box;
    width: 100%;
    padding: 11px 10px 5px 0;

    border: none;
    border-bottom: 1px solid $color-light-slate-grey;

    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    color: $color-default-white;
    background-color: transparent;

    &::placeholder {
      color: #ffffff;
    }

    // @include hover-focus {

    // }
  }
}

.form__button {
  grid-column: span 2;
  padding: 17px;
  font-size: 14px;
  line-height: 16px;
  font-weight: 700;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  border: 3px solid #ffffff;
  background: $color-transparent;
  color: #ffffff;

  @include vp-1199 {
    margin-top: 1px;
    padding-bottom: 15px;
    line-height: 18px;
  }

  @include vp-767 {
    margin-top: 10px;
    padding-bottom: 14px;
    grid-column: auto;
  }

}
